* {
  margin: 0%;
  padding: 0%;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /* background: red; */
  background-color: lightblue;
}

.timer-box {
  padding: 3vh;
  background-color: white;
  display: flex;
  gap: 8vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;}
h4 {
  font-size: 9vh;
}
.buttons {
  display: flex;
  flex-direction: row;
  gap: 12vh;
}
.buttons button {
  font-size: 3vh;
  padding: 8px;
  border-radius: 30px;
}
@media only screen and (max-width:700px)
{
  .buttons {
    display: flex;
    flex-direction: row;
    gap: 2vh;
  }
.timer-box {
  padding: 3vh;
  background-color: white;
  display: flex;
  gap: 8vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;}
}